/* LoginModal.css */

/* Keyframe animation for sliding in from the left */
@keyframes slideIn {
  from {
    left: -100%; /* Start off-screen to the left */
  }
  to {
    left: 0; /* Slide in to the center */
  }
}

/* Keyframe animation for sliding out to the left */
@keyframes slideOut {
  from {
    left: 0; /* Start at the center */
  }
  to {
    left: -100%; /* Slide out to the left */
  }
}

/* Modal container styles */
.modal-container {
  position: fixed;
  top: 0;
  width: 25%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7); /* Semi-transparent background */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; /* Ensure the modal appears above other content */
  animation: slideIn 0.45s ease-in-out; /* Apply slide-in animation */
}

/* Modal content styles */
.modal-content {
  background-color: #190c0c;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.2);
  text-align: center;
}

/* Animation for sliding out */
.modal-container.slide-out {
  animation: slideOut 0.45s ease-in-out; /* Apply slide-out animation */
}


.modal-div li{
  padding: 8px;
}